class MyDate{
  constructor(date){
    this.today = new Date(date)
    this.dateDesc = [
      '','','','','','','','','','',
      '','','','','','','','','','',
      '','','','','','','','','',''
    ]
  }

  InitArray () {
    let ret = null
    ret.length = arguments.length
    for (let i = 0; i < ret.length; i++) ret[i + 1] = arguments[i]
    return ret
  }

  InitFestive () {
    let ret = ''
    let calendar = this.today
    let month = calendar.getMonth() + 1
    let date = calendar.getDate()
    if ((month === 1) && (date === 1)) ret = ''
    if ((month === 3) && (date === 12)) ret = ''
    if ((month === 4) && (date === 5)) ret = ''
    if ((month === 5) && (date === 1)) ret = ''
    if ((month === 5) && (date === 4)) ret = ''
    if ((month === 6) && (date === 1)) ret = ''
    if ((month === 8) && (date === 1)) ret = ''
    if ((month === 8) && (date === 16)) ret = ''
    if ((month === 10) && (date === 1)) ret = '//'
    if ((month === 12) && (date === 24)) ret = ''
    if ((month === 12) && (date === 25)) ret = ''
    return ret
  }

  getNormalDateString () {
    let ret = ''
    let _d = this.InitArray('', '', '', '', '', '', '')
    ret = (this.today.getFullYear() + '' + 
          (this.today.getMonth() + 1) + '' + 
          this.today.getDate() + ' ' + 
          _d[this.today.getDay() + 1] + ' ')
    return ret
  }  

  getLocalDate () {
    let _Q = 0
    let _R = 0
    let _YY = this.today.getFullYear()
    let _MM = this.today.getMonth() + 1
    let _DD = this.today.getDate()
    _Q = Math.floor((_YY - 1977) / 4)
    _R = (_YY - 1977) % 4
    const _T = (new Date(_YY + '-' + _MM + '-' + _DD + ' ' + '12:00:00').getTime()) - new Date(_YY.toString()).getTime()
    let _N = Math.ceil(_T / 86400000) + 1
    let _n = (14 * _Q + 10.6 * (_R + 1) + _N) % 29.5
    if (_n < 0.5) {
      return this.dateDesc[28]
    }
    if (_n < 1) {
      return this.dateDesc[29]
    }
    return this.dateDesc[Math.floor(_n) - 1]
  }
}

export default MyDate